/*@import url('https://fonts.cdnfonts.com/css/avenir-lt-pro');*/

@font-face {
    font-family: 'AvenirNextLTPro-UltLt';
    src: url('../fonts/avenir-next-ultra-light.ttf') format('truetype');
    font-weight: 100;
    font-style: normal;

}

:root {
    --glow-color-projects: hsl(186 100% 69%);
    --glow-color-contact: hsl(186, 100%, 96%);
    --glow-color-email: #0d446e;
    --image-container-opacity: 0.2;

    --color-overlay-main: #101116;

    --color-glass: #0091AD;
    --color-nature: #8075FF;
    --color-fragments: #FAA275;
    --color-dad: #b53b72;
    --color-mizwak: #EFA00B;
    --color-spaceshooter: #1E3888;
    --color-text: #f9f6ee;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    scrollbar-width: none;
}


#container-opening-scene {
    width: 100svw;
    height: 100svh;
}


/* Contact Section */

#container-contact {
    background-image: linear-gradient(130deg,
            hsl(38deg 96% 39%) 0%,
            hsl(37deg 92% 37%) 0%,
            hsl(37deg 88% 35%) -1%,
            hsl(37deg 84% 33%) -1%,
            hsl(36deg 81% 31%) -1%,
            hsl(36deg 78% 29%) -1%,
            hsl(35deg 75% 26%) -1%,
            hsl(35deg 73% 24%) -1%,
            hsl(35deg 70% 22%) 0%,
            hsl(34deg 67% 19%) 0%,
            hsl(34deg 64% 17%) 2%,
            hsl(34deg 60% 15%) 4%,
            hsl(34deg 58% 12%) 7%,
            hsl(34deg 57% 10%) 12%,
            hsl(35deg 61% 7%) 20%,
            hsl(33deg 68% 4%) 35%,
            hsl(0deg 0% 0%) 99%);
    position: relative;
    width: 100svw;
    height: 100svh;
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

/* Card container remains at full height of section */
.card-container {
    position: absolute;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    width: 60svw;
    height: 70svh;
    overflow: hidden;
}

/* Scrollable card */
.card {
    width: 40svw;
    height: 75svh;
    border-radius: 2.5px;
    overflow-y: scroll;
    margin-top: 5svh;
    margin-bottom: 2vh;

}

/* Optional: Hide scrollbar on WebKit browsers */
.card::-webkit-scrollbar {
    display: none;
}


.card p {
    color: var(--color-text);
    background-color: rgba(0, 0, 0, 0.5);
    font-size: 1rem;
    /* margin-top: 1rem; */
    padding: 10px 10px 10px 10px;
    line-height: 1.7em;
}

.card p span {
    font-size: 1.5rem;
}

@media (max-width: 1024px) {
    .card-container {
        width: 90vw;
    }

    .card {
        height: 100%;
    }
}

@media (orientation: landscape) {
    .card {
        width: 70%;
    }
}

@media (orientation: portrait) {
    .card {
        width: 100%;
    }
}


body {
    max-width: 100svw;
    scrollbar-width: none;
    scroll-behavior: initial;
    -webkit-font-smoothing: antialiased;
    font-family: 'AvenirNextLTPro-UltLt';
}


footer {
    position: fixed;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 5svh;
    z-index: 25;

}

