/* Navigation Bar*/

nav {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: 100%;
    width: 60vw;
    margin: auto;
}

nav ul {
    display: flex;
    align-items: center;
    height: 100%;
    width: fit-content;

}

nav ul li {
    list-style-type: none;
    user-select: none;
    height: 100%;
    width: 12vw;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(1, 4, 6, 0.4);
    /* padding: 0 50px; */
    border-left: 2px solid #0a3353;
    transition: all ease-in-out .5s;
}

nav ul li:last-child {
    border-right: 2px solid rgba(10, 51, 83, 1.0);
}

nav ul li:hover {
    background-color: rgba(10, 51, 83, 0.75);
}



nav li {
    color: var(--color-text);
    font-weight: 500;
    font-size: 1.15rem;
    text-decoration: none;
    cursor: pointer;

}

@media (max-width: 1024px) and (orientation: landscape) {
    footer {
        height: 9svh;
    }

    nav {
        width: 90vw;
    }

    nav ul li {
        width: 20vw;
    }
}

@media (max-width: 1024px) {
    nav {
        width: 90vw;
    }
}

@media (max-width: 768px) and (orientation: portrait) {
    nav {
        width: 100vw;
    }

    nav ul li {
        width: 50vw;
    }

    nav ul {}
}


#container-projects {
    position: relative;
    min-height: 100svh;
