/* On hover, fade out the overlay */
.image-container:hover::before {
    opacity: 0;
    /* Fade out the overlay to reveal the image */
}

.image-container:hover .text-overlay {
    background-color: rgba(30, 34, 39, 0.9);
    /* Gray background on hover */
}

/* Optional: Add a transition effect to the image for smoothness */
.image-container img {
    transition: transform 0.4s ease;
}

/* Optional: Scale the image slightly on hover for a zoom effect */
.image-container:hover img {
    transform: scale(1.05);
    /* Slight zoom */
    filter: grayscale(0);

}

/*
Overlay
*/


.overlay {
    /* overscroll-behavior: contain; */
    scroll-behavior: smooth;
    position: fixed;
    top: 0;
    right: -100%;
    /* Hide off-screen */
    width: 100vw;
    height: 100%;
    background-color: rgba(30, 34, 39, 1.0);
    /* background-image: url("../img/glassharmonica/bg.png"); */
    background-position: center;
    /* background-attachment: fixed; */
    background-size: cover;

    display: flex;
    justify-content: center;
    transition: right 1.0s ease-in-out;
    /* Smooth slide-in effect */
    z-index: 1000;
    overflow-y: scroll;

}

/* OVERLAY */

.overlay.show {
    right: 0;
    /* Slide in */
}

.overlay-content {
    /* background-color: #8b5858; */
    width: 50vw;
    height: auto;
    /* background: rgba(255, 255, 255, 0.8); */
