.close-btn {
    display: flex;
    position: fixed;
    width: 25px;
    height: 25px;
    z-index: 1200;
    top: 15px;
    right: 15px;
    border: 1.5px solid white;
    border-radius: 1.5px;
    /* White border */
    background-color: transparent;
    /* Transparent background */
    cursor: pointer;
    box-sizing: border-box;
    opacity: 0;
    /* Initially hidden */
    transition: opacity 2.0s ease;
    /* Transition for fade-in */
}

/* The 'X' (cross) inside the button */
.close-btn::before,
.close-btn::after {
    content: '';
    position: absolute;
    width: 80%;
    /* Slightly smaller than the button size */
    height: 1.5px;
    /* Thickness of the cross lines */
    background-color: white;
    /* White cross */
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(45deg);
    /* First line rotated */
}

.close-btn::after {
    transform: translate(-50%, -50%) rotate(-45deg);
    /* Second line rotated */
}


.close-btn.show {
    /* display: flex; */
    opacity: 1;
}

.hide {
    display: none;
}






/* Button */

*,
*::before,
*::after {
    box-sizing: border-box;
}

button {
    touch-action: manipulation;
    /* Prevents double-tap zoom */
}

/* Common button styles */
.glowing-btn {
    position: absolute;
    z-index: 30;
    top: 1.25svh;
    cursor: pointer;
    padding: 0.35rem 1.0rem;
    border: 0.10rem solid;
    border-radius: 0.1rem;
    /* Consistent radius */
    background: none;
    font-family: 'AvenirNextLTPro-UltLt';
    perspective: 2rem;
    font-size: 1.35rem;
    font-weight: 900;
    text-align: center;
    box-shadow: inset 0px 0px 0.4rem 0px, 0px 0px 0.4rem 0px;
    animation: border-flicker 4s linear infinite;
}

/* Specific styles for the 'projects' button */
.glowing-btn-projects {
    right: 20vw;
    color: var(--glow-color-projects);
    border-color: var(--glow-color-projects);
    box-shadow: inset 0px 0px 0.4rem 0px var(--glow-color-projects),
        0px 0px 0.4rem 0px var(--glow-color-projects);
}

/* Specific styles for the 'contact' button */
.glowing-btn-contact {
    right: 32vw;
    color: var(--glow-color-contact);
    border-color: var(--glow-color-contact);
    box-shadow: inset 0px 0px 0.4rem 0px var(--glow-color-contact),
        0px 0px 0.4rem 0px var(--glow-color-contact);
}

/* e-mail button */

.glowing-btn-email {
    top: 75svh;
    left: 20svw;
    color: var(--glow-color-email);
    border-color: var(--glow-color-email);
    box-shadow: inset 0px 0px 0.4rem 0px var(--glow-color-email),
        0px 0px 0.4rem 0px var(--glow-color-email);
}

/* .glowing-btn-email>p {
    font-weight: 800;
} */


.glowing-txt-projects {
    float: left;
    /* margin-right: -0.8em; */
    -webkit-text-shadow: 0 0 0.125rem hsl(0 0% 100% / 0.3),
        0 0 0.45em var(--glow-color-projects);
    -moz-text-shadow: 0 0 0.125rem hsl(0 0% 100% / 0.3),
        0 0 0.45em var(--glow-color-projects);
    text-shadow: 0 0 0.125rem hsl(0 0% 100% / 0.3), 0 0 0.45rem var(--glow-color-projects);
    /* animation: text-flicker 3s linear infinite; */
}

.glowing-txt-contact {
    float: left;
    /* margin-right: -0.8em; */
    -webkit-text-shadow: 0 0 0.125rem hsl(0 0% 100% / 0.3),
        0 0 0.45rem var(--glow-color-contact);
    -moz-text-shadow: 0 0 0.125rem hsl(0 0% 100% / 0.3),
        0 0 0.45rem var(--glow-color-contact);
    text-shadow: 0 0 0.125rem hsl(0 0% 100% / 0.3), 0 0 0.45rem var(--glow-color-contact);
    /* animation: text-flicker 3s linear infinite; */
}

.glowing-txt-email {
    float: left;
    /* margin-right: -0.8em; */
    -webkit-text-shadow: 0 0 0.125rem hsl(0 0% 100% / 0.3),
        0 0 0.45rem var(--glow-color-email);
    -moz-text-shadow: 0 0 0.125rem hsl(0 0% 100% / 0.3),
        0 0 0.45rem var(--glow-color-email);
    text-shadow: 0 0 0.125rem hsl(0 0% 100% / 0.3), 0 0 0.45rem var(--glow-color-email);
    /* animation: text-flicker 3s linear infinite; */
}

/* Common styles for both buttons' ::before pseudo-elements */
.glowing-btn::before {
    content: "";
    position: absolute;
    cursor: pointer;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0.3;
    filter: blur(1rem);
    transform: translateY(120%) rotateX(95deg) scale(1, 0.5);
    /* pointer-events: none; */
}

/* Specific background for 'projects' button */
.glowing-btn-projects::before {
    background: var(--glow-color-projects);
}

/* Specific background for 'contact' button */
.glowing-btn-contact::before {
    background: var(--glow-color-contact);
}

.glowing-btn-email::before {
    background: var(--glow-color-email);
}


/* Common hover styles for both buttons */
.glowing-btn:hover,
.glowing-btn:focus,
.glowing-btn:active {
    text-shadow: none;
    animation: none;
    border-width: 0.2rem;
}

/* Specific hover styles for the 'projects' button */
.glowing-btn-projects:hover {
    border-color: var(--glow-color-projects);
}

/* Specific hover styles for the 'contact' button */
.glowing-btn-contact:hover {
    border-color: var(--glow-color-contact);
}

.glowing-btn-email:hover,
.glowing-btn-email:focus,
.glowing-btn-email:active {
    border-color: var(--glow-color-email);
}


.glowing-btn-projects:hover .glowing-txt-projects {
    animation: none;
}

.glowing-btn-contact:hover .glowing-txt-contact {
    animation: none;
}

.glowing-btn-email:hover .glowing-txt-email,
.glowing-btn-email:focus .glowing-txt-email,
.glowing-btn-email:active .glowing-txt-email {
    animation: none;
}



.glowing-btn-projects:hover:before {
    filter: blur(1.0rem);
    opacity: 1.0;
}

.glowing-btn-contact:hover:before {
    filter: blur(1.0rem);
    opacity: 1.0;
}

.glowing-btn-email:hover:before,
.glowing-btn-email:active:before,
.glowing-btn-email:focus:before {
    filter: blur(1.0rem);
    opacity: 1.0;
}



.glowing-btn-email::before {
    background: var(--glow-color-email);
}




